.checkbox {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 400px;
  margin: 0 auto 5px;

  &-content {
    border-radius: 4px;
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    padding: 5px 10px 5px 5px;
    cursor: pointer;

    // input {
    //   margin-right: 5px;
    // }

    &:hover {
      background: rgb(245, 245, 245);
    }
  }

  // &-delete {
  //   background: rgb(200, 200, 200);
  //   border: none;
  //   color: rgb(135, 135, 135);
  //   cursor: pointer;
  //   width: 24px;
  //   height: 24px;
  //   flex-shrink: 0;
  //   border-radius: 2px;

  //   &:hover {
  //     background: rgb(172, 172, 172);
  //   }
  // }

  &-checked {
    text-decoration: line-through;
  }
}
